<template>
  <main-layout>
    <div class="content-fluid">
      <h1>{{$route.meta.title}}</h1>
      <ul class="list-review">
        <li v-for="(review, index) in reviews" :key="index">
          <div class="reviewStars-input">
            <label :class="review.msg_rank >= 1 ? 'active' : ''"></label>
            <label :class="review.msg_rank >= 2 ? 'active' : ''"></label>
            <label :class="review.msg_rank >= 3 ? 'active' : ''"></label>
            <label :class="review.msg_rank >= 4 ? 'active' : ''"></label>
            <label :class="review.msg_rank >= 5 ? 'active' : ''"></label>
          </div>
          <div>
            <span class="list-review__name">{{review.author_name}}</span>
            <span class="list-review__date" v-if="review.msg_datetime">{{review.msg_datetime | getDate}}</span>
          </div>
          <div v-html="review.message_text">
          </div>
        </li>
      </ul>
      <review-comment></review-comment>
    </div>
  </main-layout>
</template>

<script>
  import MainLayout from '@/layouts/Main.vue'
  import ReviewComment from './ReviewComment'
  export default {
    components: {
      MainLayout, ReviewComment
    },
    data() {
      return {
        reviews: [
        {text: '  ,                ', rating: 5},
        {text: '    ZZR 400,  ,    ,    .   ! <br>  100% ,  .   " -"   .', rating: 5},
        {text: '       !!   ,, ,   !!        !!!', rating: 5},
        {text: '    ZZR-400,  ,  . :-)', rating: 5},
        {text: ' ,  .     .      . ', rating: 5},
        {text: ' , , ,         .', rating: 5},
        {text: '   ,  ,    ,  !        !', rating: 5},
        {text: '  ,   ,    !     ,    !      .   / .', rating: 5},
        {text: '    .    ))', rating: 5},
        {text: '    .         .      .  .    )))', rating: 5},
        {text: ' !!!       ! !', rating: 5},
        {text: '                ,  !!!!! , !!! !!', rating: 5},
        {text: '   GSX-R K6 1000..  ,      ,        ( ).   , ,                 . <br>      !', rating: 5},
        {text: '   Kawasaki ZX6R.     ,      3     .         !!! :-)', rating: 5},
        {text: '    GSX-R 600,   .  ,  ,        . <br> ,     . <br>P.S.:        .', rating: 5},
        {text: '    .   , ,        .      .  ,   .           ,      ))    !!!', rating: 5},
        {text: '      . ,    ,       .', rating: 5},
        {text: '  ! K,   ..', rating: 5}
        ]
      }
    },
    filters: {
      getDate: function(datetext) {
        var date = new Date(datetext)
        if (date) return `${("0" + date.getDate()).slice(-2)}.${("0" + (date.getMonth() + 1)).slice(-2)}.${date.getFullYear()}`

        return ''
      }
    },
    created(){
				this.$API.get('reviews').then(r => {
					this.reviews = r.data.data;
        });
    }
  }
</script>

<style scoped>
  .list-review{
    list-style: none;
  }
  .list-review li{
    padding: 20px 0;
    border-bottom: 1px solid #e7e7e7;
  }
  .list-review__name{
    padding-right: 10px;
    font-weight: 600;
  }
  .list-review__date{
    color: #ccc;
  }

  .reviewStars-input {
    overflow: hidden;
    zoom: 0.5;  
    position: relative;
    display: inline-block;
  }
  .reviewStars-input label {
    background: url('http://positivecrash.com/wp-content/uploads/ico-s71a7fdede6.png') no-repeat;
    background-position: 0 0;
    height: 40px;
    width: 43px;
    cursor: pointer;
    margin-right: 10px;
    display: inline-block;
  }
  .reviewStars-input label.active {
    background-position: 0 -40px;
    height: 40px;
    width: 43px;
  }
  .reviewStars-input .star-0 {
    left: 0px;
  }
  .reviewStars-input .star-1 {
    left: 53px;
  }
  .reviewStars-input .star-2 {
    left: 106px;
  }
  .reviewStars-input .star-3 {
    left: 159px;
  }
  .reviewStars-input .star-4 {
    left: 212px;
  }
  .reviewStars-input .star-5 {
    left: 265px;
  }
</style>


